#drum-machine{
    width: 30%;
    text-align: center;
    margin: 0 auto;
}
.header{
    text-align: center;
    color: wheat;
    font-size: 28px;
    font-weight: 400;
}
#display{
    height: 60px;
    width: 80%;
    border-radius: 10px;
    background-color: #f16da4;
    line-height: 60px;
    font-size: 28px;
    color: white;
    text-shadow: 3px 3px 8px #ff0000;
}
footer{
    position: absolute;
    bottom: 25px;
    font-size: 28px;
    left:40%;
    color: aqua;
    text-shadow: 3px 3px 8px #ff0000;
}


